export default {"ts":"import { Component } from 'jinge';\n\nimport { DialogAlert } from 'jinge-material/lib/dialog/dialog-alert';\nimport _tpl from './layouts.html';\n\nexport default class ExampleLayoutsCard extends Component {\n  static template = _tpl;\n\n  sendMessage() {\n    DialogAlert.show('Send a message...');\n  }\n\n  doACall() {\n    DialogAlert.show('Calling someone...');\n  }\n}\n","sass":".demo {\n  :global {\n    .md-card {\n      width: 320px;\n      margin: 4px;\n      display: inline-block;\n      vertical-align: top;\n    }\n\n    .md-card-example {\n      .md-subhead {\n        .md-icon {\n          $size: 16px;\n\n          width: $size;\n          min-width: $size;\n          height: $size;\n          font-size: $size !important;\n        }\n\n        span {\n          vertical-align: middle;\n        }\n      }\n\n      .card-reservation {\n        margin-top: 8px;\n        display: flex;\n        align-items: center;\n        justify-content: space-between;\n\n        .md-icon {\n          margin: 8px;\n        }\n      }\n\n      .md-button-group {\n        display: flex;\n\n        .md-button {\n          min-width: 60px;\n          border-radius: 2px;\n        }\n      }\n    }\n  }\n}\n","html":"<!-- import style from './layouts.scss' -->\n<div :class=\"style.demo\">\n  <md-card>\n    <md-card-media>\n      <img src=\"assets/examples/card-image-1.jpg\" alt=\"People\"/>\n    </md-card-media>\n\n    <md-card-header>\n      <div class=\"md-title\">Title goes here</div>\n      <div class=\"md-subhead\">Subtitle here</div>\n    </md-card-header>\n\n    <md-card-actions>\n      <md-button>Action</md-button>\n      <md-button>Action</md-button>\n    </md-card-actions>\n\n    <md-card-content>\n      Lorem ipsum dolor sit amet, consectetur adipisicing elit. Optio itaque ea, nostrum odio. Dolores, sed accusantium quasi non, voluptas eius illo quas, saepe voluptate pariatur in deleniti minus sint. Excepturi.\n    </md-card-content>\n  </md-card>\n\n  <md-card>\n    <md-card-header>\n      <md-avatar>\n        <img src=\"assets/examples/avatar.png\" alt=\"Avatar\"/>\n      </md-avatar>\n\n      <div class=\"md-title\">Title goes here</div>\n      <div class=\"md-subhead\">Subtitle here</div>\n    </md-card-header>\n\n    <md-card-media>\n      <img src=\"assets/examples/card-image-1.jpg\" alt=\"People\"/>\n    </md-card-media>\n\n    <md-card-content>\n      Lorem ipsum dolor sit amet, consectetur adipisicing elit. Optio itaque ea nostrum.\n    </md-card-content>\n\n    <md-card-actions>\n      <md-button>Action</md-button>\n      <md-button>Action</md-button>\n    </md-card-actions>\n  </md-card>\n\n  <md-card class=\"md-card-example\">\n    <md-card-area inset>\n      <md-card-media ratio=\"16:9\">\n        <img src=\"assets/examples/card-example.jpg\" alt=\"Coffee House\"/>\n      </md-card-media>\n\n      <md-card-header>\n        <h2 class=\"md-title\">Coffee House</h2>\n        <div class=\"md-subhead\">\n          <md-icon-my_location/>\n          <span>2 miles</span>\n        </div>\n      </md-card-header>\n\n      <md-card-content>\n        Illy Coffee served with a complimentary Leonidas Belgian Chocolate with all beverages.\n      </md-card-content>\n    </md-card-area>\n\n    <md-card-content>\n      <h3 class=\"md-subheading\">Today's availability</h3>\n      <div class=\"card-reservation\">\n        <md-icon-access_time/>\n        <div class=\"md-button-group\">\n          <md-button>5:30PM</md-button>\n          <md-button>7:30PM</md-button>\n          <md-button>9:00PM</md-button>\n        </div>\n      </div>\n    </md-card-content>\n\n    <md-card-actions>\n      <md-button class=\"md-primary\">Reserve</md-button>\n    </md-card-actions>\n  </md-card>\n\n  <md-card>\n    <md-card-area>\n      <md-card-media>\n        <img src=\"assets/examples/card-image-1.jpg\" alt=\"People\"/>\n      </md-card-media>\n\n      <md-card-header>\n        <div class=\"md-title\">Actions left aligned</div>\n        <div class=\"md-subhead\">Subtitle here</div>\n      </md-card-header>\n\n      <md-card-content>\n        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Optio itaque ea, nostrum odio. Dolores, sed accusantium quasi non, voluptas eius illo quas, saepe voluptate pariatur in deleniti minus sint. Excepturi.\n      </md-card-content>\n    </md-card-area>\n\n    <md-card-actions alignment=\"left\">\n      <md-button>Action</md-button>\n      <md-button>Action</md-button>\n    </md-card-actions>\n  </md-card>\n\n  <md-card>\n    <md-card-media>\n      <md-ripple>\n        <img src=\"assets/examples/card-image-1.jpg\" alt=\"People\"/>\n      </md-ripple>\n    </md-card-media>\n\n    <md-card-actions>\n      <md-button class=\"md-icon-button\">\n        <md-icon>favorite</md-icon>\n      </md-button>\n\n      <md-button class=\"md-icon-button\">\n        <md-icon>bookmark</md-icon>\n      </md-button>\n\n      <md-button class=\"md-icon-button\">\n        <md-icon>share</md-icon>\n      </md-button>\n    </md-card-actions>\n  </md-card>\n\n  <md-card>\n    <md-card-media>\n      <img src=\"assets/examples/card-image-2.jpg\" alt=\"People\"/>\n    </md-card-media>\n\n    <md-card-content>\n      Lorem ipsum dolor sit amet, consectetur adipisicing elit. Optio itaque ea nostrum.\n    </md-card-content>\n  </md-card>\n\n  <md-card>\n    <md-card-header>\n      <md-card-header-text>\n        <div class=\"md-title\">Actions left aligned</div>\n        <div class=\"md-subhead\">Subtitle here</div>\n      </md-card-header-text>\n\n      <md-menu size=\"big\" placement=\"bottom-end\">\n        <md-button class=\"md-icon-button\" slot-pass:default>\n          <md-icon-more_vert/>\n        </md-button>\n\n        <_slot slot-pass:content>\n          <md-menu-item on:click=\"doACall\">\n            <span>Call</span>\n            <md-icon-phone/>\n          </md-menu-item>\n\n          <md-menu-item on:click=\"sendMessage\">\n            <span>Send a message</span>\n            <md-icon-message/>\n          </md-menu-item>\n        </_slot>\n      </md-menu>\n    </md-card-header>\n\n    <md-card-content>\n      Lorem ipsum dolor sit amet, consectetur adipisicing elit. Optio itaque ea, nostrum odio. Dolores, sed accusantium quasi non, voluptas eius illo quas, saepe voluptate pariatur in deleniti minus sint. Excepturi.\n    </md-card-content>\n\n    <md-card-actions alignment=\"left\">\n      <md-button>Action</md-button>\n      <md-button>Action</md-button>\n    </md-card-actions>\n  </md-card>\n</div>"}