export default {"ts":"import { Component } from 'jinge';\n\nimport _tpl from './media.html';\n\nexport default class ExampleMediaCard extends Component {\n  static template = _tpl;\n}\n","sass":".demo {\n  :global {\n    .md-card {\n      width: 320px;\n      margin: 4px;\n      display: inline-block;\n      vertical-align: top;\n    }\n  }\n}\n","html":"<!-- import style from './media.scss' -->\n<div :class=\"style.demo\">\n  <md-card>\n    <md-card-header>\n      <md-card-header-text>\n        <div class=\"md-title\">Media card</div>\n        <div class=\"md-subhead\">Normal size</div>\n      </md-card-header-text>\n\n      <md-card-media>\n        <img src=\"assets/examples/avatar-2.jpg\" alt=\"People\"/>\n      </md-card-media>\n    </md-card-header>\n\n    <md-card-actions>\n      <md-button>Action</md-button>\n      <md-button>Action</md-button>\n    </md-card-actions>\n  </md-card>\n\n  <md-card>\n      <md-card-header>\n        <md-card-header-text>\n          <div class=\"md-title\">Media card</div>\n          <div class=\"md-subhead\">Medium size</div>\n        </md-card-header-text>\n  \n        <md-card-media medium>\n          <img src=\"assets/examples/card-weather.png\" alt=\"People\"/>\n        </md-card-media>\n      </md-card-header>\n  \n      <md-card-actions>\n        <md-button>Action</md-button>\n        <md-button>Action</md-button>\n      </md-card-actions>\n    </md-card>\n  \n    <md-card>\n      <md-card-header>\n        <md-card-header-text>\n          <div class=\"md-title\">Media card</div>\n          <div class=\"md-subhead\">Big size</div>\n        </md-card-header-text>\n  \n        <md-card-media big>\n          <img src=\"assets/examples/cover.png\" alt=\"People\"/>\n        </md-card-media>\n      </md-card-header>\n  \n      <md-card-actions>\n        <md-button>Action</md-button>\n        <md-button>Action</md-button>\n      </md-card-actions>\n    </md-card>\n  \n    <md-card>\n      <md-card-media-actions>\n        <md-card-media>\n          <img src=\"assets/examples/villains.png\" alt=\"Cover\"/>\n        </md-card-media>\n  \n        <md-card-actions>\n          <md-button class=\"md-icon-button\">\n            <md-icon-favorite/>\n          </md-button>\n  \n          <md-button class=\"md-icon-button\">\n            <md-icon-bookmark/>\n          </md-button>\n  \n          <md-button class=\"md-icon-button\">\n            <md-icon-share/>\n          </md-button>\n        </md-card-actions>\n      </md-card-media-actions>\n    </md-card>\n</div>"}