export default {"ts":"import { Component } from 'jinge';\n\nimport _tpl from './media-cover.html';\n\nexport default class ExampleMediaCoverCard extends Component {\n  static template = _tpl;\n}\n","sass":".demo {\n  :global {\n    .md-card {\n      width: 320px;\n      margin: 4px;\n      display: inline-block;\n      vertical-align: top;\n    }\n  }\n}\n","html":"<!-- import style from './media-cover.scss' -->\n<div :class=\"style.demo\">\n  <md-card>\n    <md-card-media-cover>\n      <md-card-media ratio=\"16:9\">\n        <img src=\"assets/examples/card-sky.jpg\" alt=\"Skyscraper\"/>\n      </md-card-media>\n\n      <md-card-area>\n        <md-card-header>\n          <span class=\"md-title\">Without text background</span>\n          <span class=\"md-subhead\">I bet you can't read this</span>\n        </md-card-header>\n\n        <md-card-actions>\n          <md-button>Neither this</md-button>\n          <md-button>Or this</md-button>\n        </md-card-actions>\n      </md-card-area>\n    </md-card-media-cover>\n  </md-card>\n\n  <md-card>\n    <md-card-media-cover textScrim>\n      <md-card-media ratio=\"16:9\">\n        <img src=\"assets/examples/card-sky.jpg\" alt=\"Skyscraper\"/>\n      </md-card-media>\n\n      <md-card-area>\n        <md-card-header>\n          <span class=\"md-title\">Gradient background</span>\n          <span class=\"md-subhead\">16/9 image</span>\n        </md-card-header>\n\n        <md-card-actions>\n          <md-button>Action</md-button>\n          <md-button>Action</md-button>\n        </md-card-actions>\n      </md-card-area>\n    </md-card-media-cover>\n  </md-card>\n\n  <md-card>\n    <md-card-media-cover solid>\n      <md-card-media ratio=\"4:3\">\n        <img src=\"assets/examples/card-sky.jpg\" alt=\"Skyscraper\"/>\n      </md-card-media>\n\n      <md-card-area>\n        <md-card-header>\n          <span class=\"md-title\">Solid background</span>\n          <span class=\"md-subhead\">4/3 image</span>\n        </md-card-header>\n      </md-card-area>\n    </md-card-media-cover>\n  </md-card>\n\n  <md-card>\n    <md-card-media-cover solid>\n      <md-card-media ratio=\"1:1\">\n        <img src=\"assets/examples/card-sky.jpg\" alt=\"Skyscraper\"/>\n      </md-card-media>\n\n      <md-card-area>\n        <md-card-header>\n          <span class=\"md-title\">Solid background</span>\n          <span class=\"md-subhead\">1/1 image</span>\n        </md-card-header>\n\n        <md-card-actions>\n          <md-button class=\"md-icon-button\">\n            <md-icon-favorite/>\n          </md-button>\n\n          <md-button class=\"md-icon-button\">\n            <md-icon-share/>\n          </md-button>\n        </md-card-actions>\n      </md-card-area>\n    </md-card-media-cover>\n  </md-card>\n</div>"}