export default {"ts":"import { Component } from 'jinge';\n\nimport _tpl from './theme.html';\n\nexport default class ExampleThemeCard extends Component {\n  static template = _tpl;\n}\n","sass":"@import '~jinge-material/style/palette.scss';\n\n.demo {\n  :global {\n    .md-purple-card {\n      background: md-get-palette-color(purple, 500);\n    }\n\n    .md-orange-card {\n      background: md-get-palette-color(orange, 500);\n    }\n\n    .md-card {\n      width: 320px;\n      margin: 4px;\n      display: inline-block;\n      vertical-align: top;\n    }\n  }\n}\n","html":"<!-- import style from './theme.scss' -->\n<div :class=\"style.demo\">\n  <md-card class=\"md-primary\">\n    <md-card-header>\n      <md-card-header-text>\n        <div class=\"md-title\">Primary color</div>\n        <div class=\"md-subhead\">Subtitle here</div>\n      </md-card-header-text>\n\n      <md-card-media>\n        <img src=\"assets/examples/avatar-2.jpg\" alt=\"Avatar\"/>\n      </md-card-media>\n    </md-card-header>\n\n    <md-card-actions>\n      <md-button>Action</md-button>\n      <md-button>Action</md-button>\n    </md-card-actions>\n  </md-card>\n\n  <md-card class=\"md-accent\" withHover>\n    <md-ripple>\n      <md-card-header>\n        <div class=\"md-title\">Accent color</div>\n        <div class=\"md-subhead\">With hover and ripple effects</div>\n      </md-card-header>\n\n      <md-card-content>\n        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Optio itaque ea, nostrum odio. Dolores, sed accusantium quasi non.\n      </md-card-content>\n\n      <md-card-actions>\n        <md-button>Action</md-button>\n        <md-button>Action</md-button>\n      </md-card-actions>\n    </md-ripple>\n  </md-card>\n\n  <md-card class=\"md-primary md-purple-card\" withHover>\n    <md-ripple>\n      <md-card-header>\n        <div class=\"md-title\">Purple primary color</div>\n        <div class=\"md-subhead\">With hover and ripple effects</div>\n      </md-card-header>\n\n      <md-card-content>\n        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Optio itaque ea, nostrum odio. Dolores, sed accusantium quasi non.\n      </md-card-content>\n    </md-ripple>\n  </md-card>\n\n  <md-card class=\"md-primary md-orange-card\" withHover>\n    <md-ripple>\n      <md-card-header>\n        <div class=\"md-title\">Orange primary color</div>\n        <div class=\"md-subhead\">With hover and ripple effects</div>\n      </md-card-header>\n\n      <md-card-content>\n        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Optio itaque ea, nostrum odio. Dolores, sed accusantium quasi non.\n      </md-card-content>\n    </md-ripple>\n  </md-card>\n</div>"}